import React, { useState } from 'react';
import { ChevronLeft, ChevronRight } from 'lucide-react';

const Presentation = () => {
  const [currentSlide, setCurrentSlide] = useState(0);

  const slides = [
    {
      title: "Breaking Language Barriers",
      subtitle: "A Multi-Method Investigation at The New School",
      stats: [
        { number: "3,750+", label: "International Students" },
        { number: "100+", label: "Countries" },
        { number: "57%", label: "Report Barriers" },
        { number: "20", label: "Study Participants" }
      ]
    },
    {
      title: "Current Challenges",
      points: [
        "Language Barriers - Significant impact on healthcare access",
        "Medical Terminology - Complex terminology creates hurdles",
        "Cultural Differences - Affect interpretation of healthcare"
      ]
    },
    {
      title: "Methodology",
      sections: [
        {
          title: "User Observation",
          points: ["Lab Setup", "Data Collection", "Response Tracking"]
        },
        {
          title: "Survey Design",
          points: ["Pre/Post Observation", "Likert Scale Ratings", "Open Feedback"]
        },
        {
          title: "GOMS Analysis",
          points: ["Task Completion", "Decision Mapping", "Cognitive Assessment"]
        }
      ]
    },
    {
      title: "Timeline",
      phases: [
        {
          title: "Month One: Foundation",
          tasks: ["IRB Approval", "Team Assembly", "Protocol Setup"]
        },
        {
          title: "Month Two: Data Collection",
          tasks: ["User Observations", "Survey Administration", "Initial Analysis"]
        },
        {
          title: "Month Three: Analysis",
          tasks: ["Data Synthesis", "Report Preparation", "Final Presentation"]
        }
      ]
    },
    {
      title: "Expected Outcomes",
      sections: [
        {
          title: "Service Improvements",
          points: ["Multi-language Support", "Cultural Guidelines", "Protocol Updates"]
        },
        {
          title: "Long-term Impact",
          points: ["Improved Access", "Enhanced Satisfaction", "Better Outcomes"]
        }
      ]
    }
  ];

  const nextSlide = () => {
    setCurrentSlide((prev) => (prev + 1) % slides.length);
  };

  const prevSlide = () => {
    setCurrentSlide((prev) => (prev - 1 + slides.length) % slides.length);
  };

  const renderSlideContent = (slide) => {
    switch (currentSlide) {
      case 0:
        return (
          <div className="space-y-8">
            <h1 className="text-4xl font-bold text-center mb-4">{slide.title}</h1>
            <h2 className="text-xl text-center text-gray-300">{slide.subtitle}</h2>
            <div className="grid grid-cols-2 gap-6 mt-8">
              {slide.stats.map((stat, index) => (
                <div key={index} className="text-center p-4 bg-indigo-900/30 rounded-lg">
                  <div className="text-3xl font-bold text-indigo-400">{stat.number}</div>
                  <div className="text-sm mt-2 text-gray-300">{stat.label}</div>
                </div>
              ))}
            </div>
          </div>
        );

      case 1:
        return (
          <div className="space-y-6">
            <h2 className="text-3xl font-bold text-center mb-8">{slide.title}</h2>
            <ul className="space-y-4">
              {slide.points.map((point, index) => (
                <li key={index} className="flex items-center space-x-3 bg-indigo-900/30 p-4 rounded-lg">
                  <div className="h-2 w-2 bg-indigo-400 rounded-full"></div>
                  <span>{point}</span>
                </li>
              ))}
            </ul>
          </div>
        );

      case 2:
        return (
          <div className="space-y-6">
            <h2 className="text-3xl font-bold text-center mb-8">{slide.title}</h2>
            <div className="grid grid-cols-3 gap-6">
              {slide.sections.map((section, index) => (
                <div key={index} className="bg-indigo-900/30 p-4 rounded-lg">
                  <h3 className="text-xl font-semibold mb-4 text-indigo-400">{section.title}</h3>
                  <ul className="space-y-2">
                    {section.points.map((point, idx) => (
                      <li key={idx} className="text-sm">{point}</li>
                    ))}
                  </ul>
                </div>
              ))}
            </div>
          </div>
        );

      case 3:
        return (
          <div className="space-y-6">
            <h2 className="text-3xl font-bold text-center mb-8">{slide.title}</h2>
            <div className="space-y-6">
              {slide.phases.map((phase, index) => (
                <div key={index} className="bg-indigo-900/30 p-4 rounded-lg">
                  <h3 className="text-xl font-semibold mb-2 text-indigo-400">{phase.title}</h3>
                  <div className="flex gap-4">
                    {phase.tasks.map((task, idx) => (
                      <span key={idx} className="text-sm px-3 py-1 bg-indigo-800/30 rounded-full">
                        {task}
                      </span>
                    ))}
                  </div>
                </div>
              ))}
            </div>
          </div>
        );

      case 4:
        return (
          <div className="space-y-6">
            <h2 className="text-3xl font-bold text-center mb-8">{slide.title}</h2>
            <div className="grid grid-cols-2 gap-6">
              {slide.sections.map((section, index) => (
                <div key={index} className="bg-indigo-900/30 p-4 rounded-lg">
                  <h3 className="text-xl font-semibold mb-4 text-indigo-400">{section.title}</h3>
                  <ul className="space-y-2">
                    {section.points.map((point, idx) => (
                      <li key={idx} className="text-sm">{point}</li>
                    ))}
                  </ul>
                </div>
              ))}
            </div>
          </div>
        );

      default:
        return null;
    }
  };

  return (
    <div className="w-full h-screen bg-gray-900 text-white p-8">
      <div className="max-w-4xl mx-auto h-full flex flex-col">
        <div className="flex-1 relative">
          <div className="absolute inset-0 flex items-center justify-center">
            {renderSlideContent(slides[currentSlide])}
          </div>
        </div>
        
        <div className="flex justify-between items-center mt-8">
          <button
            onClick={prevSlide}
            className="p-2 rounded-full bg-indigo-600 hover:bg-indigo-700 transition-colors"
          >
            <ChevronLeft className="w-6 h-6" />
          </button>
          
          <div className="text-sm">
            Slide {currentSlide + 1} of {slides.length}
          </div>
          
          <button
            onClick={nextSlide}
            className="p-2 rounded-full bg-indigo-600 hover:bg-indigo-700 transition-colors"
          >
            <ChevronRight className="w-6 h-6" />
          </button>
        </div>
      </div>
    </div>
  );
};

export default Presentation;